.sidebar{
    width: 270px;
    background: var(--bg-black-100);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10;
    display: flex;
    padding: 30px;
    align-items: center;
    justify-content: center;
    height: 100%;
    border-right: 1px solid var(--bg-black-50);
}

.sidebar .logo{
    position: absolute;
    top: 50px;
    font-size: 30px;
    text-transform: capitalize;
}

.sidebar .logo a{
    color: var(--text-black-900);
    font-weight: 700;
    padding: 15px 7px;
    font-size: 30px;
    letter-spacing: 5px;
    position: relative;
}

.sidebar .logo a span{
    font-family: "Clicker Script", cursive;
    font-size: 40px;
}

.sidebar .logo a::before{
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    border-bottom: 5px solid var(--skin-color);
    border-left: 5px solid var(--skin-color);
    bottom: 0;
    left: 0;
}

.sidebar .logo a::after{
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    border-top: 5px solid var(--skin-color);
    border-right: 5px solid var(--skin-color);
    top: 0;
    right: 5%;
}

.sidebar .nav-toggler{
    height: 40px;
    width: 45px;
    border: 1px solid var(--bg-black-50);
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius: 5px;
    background: var(--bg-black-100);
    display: none;
    align-items: center;
    justify-content: center;
}

.sidebar .nav-toggler span{
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display: inline-block;
    position: relative;
}

.sidebar .nav-toggler span::before{
    content: "";
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    position: absolute;
    top: -6px;
    left: 0;
}

.sidebar .nav-toggler span::after{
    content: "";
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    position: absolute;
    top: 6px;
    left: 0;
}

.sidebar .nav{
    margin-top: 50px;
}

.sidebar .nav li{
    margin-bottom: 20px;
    display: block;
    text-align: center;
}

.sidebar .nav li a {
    font-size: 16px;
    font-weight: 600;
    display: block;
    color: var(--text-black-900);
    padding: 5px 15px;
    border-bottom: 1px solid var(--bg-black-50);
}

.sidebar .nav li a.active{
    color: var(--skin-color);
}

.sidebar .nav li a i{
    margin-right: 15px;
    float: left;
}

@media screen and (max-width: 1199px) {
    .sidebar {
        left: -270px;
    }
}